import { useEffect, useState, lazy } from 'react';
import { useNavigate } from 'react-router-dom';
import { first } from 'rxjs/operators';
import { getCompanyDetails, getCompanyOrgChartPositions } from '../../../services/company.service';
import { Button } from '../../common';
import cls from './CompanyCards.module.scss';

import CompanyOrganisationalChart from '../../OnboardingEntries/CompanyOrganizationalChart/CompanyOrganizationalChart';
import { setShowMenu } from '../../../reduxStore/actions/flowActions';
import { store } from '../../../reduxStore/store';
import { isMobile } from 'react-device-detect';
const PanZoomComponent = lazy(() => import('../../common/PanZoomComponent/PanZoomComponent'));

export const CompanyOrgChartCard = props => {
	const navigate = useNavigate();
	const [loadingChart, setLoadingChart] = useState(false);
	function redirectToChartScreen() {
		store.dispatch(setShowMenu(false));
		setLoadingChart(false);
		navigate('/chart');
	}

	useEffect(() => {
		setLoadingChart(true);
		getCompanyOrgChartPositions()
			.pipe(first())
			.subscribe({
				complete: () => setLoadingChart(false),
			});
	}, []);

	const { initVersion } = props;

	return initVersion ? (
		<LabeledContainer label={''}>
			<div className={cls.borderedCardHost}>
				<div className={cls.borderedCardContainer}>
					<p>{'Begin setting up your Company by creating departments and positions.'}</p>
					<Button light loading={loadingChart} title={'Start Setup'} clicked={() => redirectToChartScreen()} />
				</div>
			</div>
		</LabeledContainer>
	) : (
		!isMobile && <div>
			<Button title={'View Roles and Departments'} clicked={() => redirectToChartScreen()} style={{}} />
		</div>
	);
};
export const PreviewChart = () => {
	return (
		<PanZoomComponent zoomStep={1} controlsContainerClass={cls.panZoomControlsContainer} panZoomProps={{ autoCenterZoomLevel: 0.65, className: cls.panZoomComponent, style: { cursor: 'grab' } }}>
			<CompanyOrganisationalChart preview />
		</PanZoomComponent>
	);
};

const LabeledContainer = props => {
	const { label, children } = props;
	return (
		<div className={cls.labeledContainerHost}>
			<p className={cls.labeledContainerLabel}>{label}</p>
			{children}
		</div>
	);
};

export const CompanyOverview = props => {
	const { companyData } = props;
	const [companyDetails, setCompanyDetails] = useState(companyData);

	useEffect(() => {
		getCompanyDetails()
			.pipe(first())
			.subscribe({
				next: data => {
					setCompanyDetails({ ...data });
				},
			});
	}, []);
	return (
		<div className={cls.host}>
			<CompanyOrgChartCard initVersion={companyDetails.departments_count == 0} />
		</div>
	);
};
